				<image src="../../static/imgs/smile2.png" v-else></image>
			</view>
			<text :class="{del:quiet}" class="music" @tap="switchMusic()">音效</text>
			<view>剩余：{{getRestBoomNum()}}</view>
		</view>
		<view class="contentMap">
